* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

header {
  font-size: 2em;
  display: flex;
  justify-content: center;
  padding: 20px 0;
}

.App {
  padding: 50px 20%;
  background: #eee;
  min-height: 100vh;
}

.HeightCalculator {
  border: 1px solid #000;
  margin-bottom: 50px;
}
.heightInfo {
  background: #ea89fe;
  padding: 10px 0;
  display: flex;
  justify-content: center;
}
.heightInput {
  margin: 20px 10px;
}
